import React, { useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { Button } from "@mui/material";
import ArrowLeftIcon from "@mui/icons-material/ArrowBack";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { questions, labels } from "common";
import AppointmentService from "../../../services/appointment.services";

function ServicesSurveySummary() {
  const location = useLocation();
  const navigate = useNavigate();
  const summaryArray = location?.state?.values;
  const [isActionLoading, setIsActionLoading] = useState(false);

  const appointmentAction = async (summaryArray) => {
    console.log(
      `appointmentAction - summaryArray : ${JSON.stringify(summaryArray)}`
    );

    setIsActionLoading(true);
    const response = await AppointmentService.updateAppointment(
      summaryArray?.appointmentId,
      { travelPreferences: JSON.stringify(summaryArray) }
    );
    // setMessage(!!response.message && response.message);
    setIsActionLoading(false);

    if (!!!response.success && !response.success) return;

    navigate("/travel-success", {
      state: { appointmentId: summaryArray?.appointmentId },
    });
    // setIsActionLoading(false);
    console.log(`appointmentAction - END`);
  };

  return (
    <>
      <div className="content success-page-cont">
        <div className="container-fluid">
          <div className="row justify-content-center">
            <div className="col-lg-6">
              <div className="card">
                <div className="d-flex flex-row justify-content-end mx-3 mt-3">
                  <Link
                    to="/services-survey"
                    className="btn btn-primary bd-highlight"
                    state={{ ...location?.state?.values, page: 4 }}
                  >
                    <i className="fas  fa-arrow-left"></i> Back to survey
                  </Link>
                </div>
                <h3 className="p-2 bd-highlight text-center">
                  Review Preferences
                </h3>
                {questions?.map((q) =>
                  summaryArray[q?.selectionCondition] === "Yes" ? (
                    <div className="card-body d-flex justify-content-center">
                      <ul className="list-group" style={{ width: "40rem" }}>
                        {" "}
                        <h4 className="mt-n4" style={{ textAlign: "start" }}>
                          {q?.title} {!!q?.fields}
                        </h4>
                        {q?.fields?.map((f) => (
                          <li className="list-group-item d-flex justify-content-between align-items-center py-1">
                            <div className="col-6">{labels[f.name]}</div>
                            <div className="col-6">{summaryArray[f.name]}</div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  ) : (
                    <div className="card-body d-flex justify-content-center">
                      <ul className="list-group" style={{ width: "40rem" }}>
                        {" "}
                        <h4 className="mt-n4" style={{ textAlign: "start" }}>
                          {q.title}
                        </h4>
                        <li className="list-group-item d-flex justify-content-between align-items-center py-1">
                          <div className="col-12">
                            {" "}
                            {"Not interested, maybe later"}
                          </div>
                        </li>
                      </ul>
                    </div>
                  )
                )}
                <div className="card-body d-flex justify-content-center">
                  {/* <img
                    src={servive1Banner}
                    className="img-fluid p-5"
                    alt="Your Medical Portal Login"
                  /> */}
                  <p className="card-text">
                    <button
                      to="/travel-success"
                      className="btn btn-primary"
                      onClick={(e) => appointmentAction(summaryArray)}
                      disabled={isActionLoading}
                    >
                      Confirm
                      {isActionLoading && (
                        <span className="spinner-border spinner-border-sm mb-1 mx-1"></span>
                      )}
                    </button>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ServicesSurveySummary;
